var that;
export default {
	name: 'goodslist',
	props: {
		text: {
			type: String,
			default: '没有更多了'
		},
		tablist: {
			type: Array
		},
		color: {
			type: String,
			default: '#b3b3b3'
		},
		selectedColor: {
			type: String,
			default: '#b000dd'
		},
		backgroundColor: {
			type: String,
			default: '#FFFFFF'
		}
	},
	data() {
		return {};
	},
	computed: {},
	methods: {
		switchTab(e) {
			let url = e.currentTarget.dataset.url;
			uni.reLaunch({
				url:url
			});
		}
	},
	created() {}
};
